.add {
    width: 70%;
    margin-left: max(5vw, 24px);
    margin-top: 50px;
    color: #6d6d6d;
    font-size: 16px;
}

.add form {
    gap: 20px;
}

.add-image-upload {
    width: 120px;
}

.add-product-name, .add-product-description {
    width: max(40%, 220px);
}

.add-product-name input, .add-product-description textarea {
    padding: 10px;
}

.add-category-price {
    /* display: flex; */
    gap: 30px;
}

.add-category-price select, .add-category-price input {
    max-width: 120px;
    padding: 10px;
}

.add-btn {
    max-width: 120px;
    border: none;
    padding: 10px;
    background-color: black;
    color: white;
    cursor: pointer;
}