.explore-menu {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.explore-menu h1 {
    color: black;
    font-weight: 500;
}

.explore-menu-text {
    max-width: 60%;
    color: grey;
}

.explore-menu-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    text-align: center;
    margin: 20px 0px;
    overflow-x: scroll;
}

.explore-menu-list::-webkit-scrollbar {
    display: none;
}

.explore-menu-list-item img {
    width: 7.5vw;
    height: 8.5vw;
    min-width: 80px;
    cursor: pointer;
    border-radius: 50%;
    transition: 0.2s;
}

.explore-menu-list-item p {
    margin-top: 10px;
    color: black;
    font-size: max(1.4vw, 16px);
    cursor: pointer;
    /* border-radius: 20%; */
    transition: 0.2s;
}

.explore-menu hr {
    margin: 10px 0px;
    height: 2px;
    background-color: grey;
    border: none;
}

.explore-menu-list-item .active {
    /* border: 4px solid tomato; */
    color: tomato;
    padding: 2px;
    text-decoration: underline tomato 4px;
}

.message {
    color: gray;
    font-size: 14px;
    font-style: italic;
}

@media (max-width:1050px) {
    .explore-menu-text {
        max-width: 100%;
        font-size: 14px;
    }
}